.consultation {
  background: #44456A;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 120px 0;
}

.consultation_title {
  font-style: normal;
  font-weight: 500;
  font-size: 34px;
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 35px;
}

.consultation_button {
  border: 1px solid white;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
  padding: 17px 14px;
  background: transparent;
  transition: all 0.3s;

  &:hover {
    cursor: pointer;
    background: rgba($color: white, $alpha: 0.3);
  }
}

@media(max-width: 767px) {
  .consultation {
    padding: 100px 15px;
  }

  .consultation_title {
    font-size: 26px;
    margin-bottom: 35px;
  }

  .consultation_button {
    width: 100%;
  }
}
