.refusals {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 120px;
}

.refusals_title {
  display: inline-block;
  width: 100%;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 50px;
}

.refusals_item {
  flex-shrink: 0;
  flex-basis: 23%;
}

.refusals_description {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 120%;
  color: #000000;
  margin-top: 25px;
}

@media(max-width: 767px) {
  .refusals {
    padding: 100px 15px 50px;
    flex-direction: column;
  }

  .refusals_title {
    font-size: 26px;
    margin-bottom: 40px;
  }

  .refusals_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }

  .refusals_description {
    text-align: center;
  }
}
