.header {
  background: #FFFFFF;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.08);

  & .center {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 32px 15px;
  }
}

.header_button {
  background: #203586;
  padding: 13px 24px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 110%;
  color: #FFFFFF;
  border: none;
  transition: all 0.3s;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    background: #2c4295;
  }
}

@media(max-width: 767px) {
  .header_logo {
    width: 100px;
  }

  .header .center {
    padding: 8px 15px;
  }
}
