.footer {
  background: #343532;
  padding-top: 60px;
  padding-bottom: 66px;

  & .center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.footer_text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  text-align: center;
  color: #B9B9B9;
  margin-bottom: 15px;
}

.footer_logo {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 140%;
  color: #FFFFFF;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}
