.steps {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 125px;
  box-sizing: border-box;
  margin-bottom: 120px;
}

.steps_title {
  display: inline-block;
  width: 100%;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 110%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 50px;
}

.steps_item {
  flex-basis: 29.8%;
  flex-shrink: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.steps_subtitle {
  font-style: normal;
  font-weight: 600;
  font-size: 21px;
  line-height: 110%;
  text-align: center;
  color: #000000;
  margin-top: 25px;
  margin-bottom: 15px;
}

.steps_description {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 140%;
  text-align: center;
  color: #000000;
}

@media(max-width: 767px) {
  .steps_title {
    font-size: 26px;
    margin-bottom: 40px;
  }

  .steps {
    padding: 0;
    flex-direction: column;
  }

  .steps_subtitle {
    margin-top: 20px;
  }

  .steps_item {
    margin-top: 50px;
  }
}
