.quiz {
  padding: 80px 0 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.question {
  background: #FFFFFF;
  border: 1px solid #B9B9B9;
  max-width: 966px;
  width: calc(100% - 30px);
  min-height: 662px;
  height: 100%;
  box-sizing: border-box;
  display: none;
  flex-direction: column;
  margin-right: 15px;
  margin-left: 15px;

  &.active {
    display: flex;
  }
}

.question_title {
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
  display: inline-block;
  text-transform: uppercase;
  width: 100%;
  background: linear-gradient(180deg, #2C4295 0%, #0082C9 100%, rgba(53, 44, 149, 0) 100%);
  padding: 28px 0;
}

.question_body {
  padding: 40px 100px;
  display: flex;
  flex-direction: column;
  height: 100%;
  flex-basis: 100%;
  flex-shrink: 0;
  flex-grow: 1;
}

.question_steps-wrapper {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 120%;
  color: #B8B8B8;
}

.question_scale {
  background: #E3E3E3;
  border-radius: 20px;
  display: block;
  width: 100%;
  height: 8px;
  margin-top: 8px;
  margin-bottom: 20px;
  position: relative;
}

.question_slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: calc(100%/8 * 1);
  height: 8px;
  border-radius: 20px;
  background: #2B4FA9;
}

.question_label {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 12px;
  margin-top: 30px;
}

.question_text-input {
  background: #F9F9F9;
  border: 1px solid #DFDFDF;
  max-width: 300px;
  width: 100%;
  margin-bottom: 12px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: black;
  padding: 15px 20px;
  box-sizing: border-box;

  &--big {
    max-width: 350px;
  }

  &--half {
    flex-basis: calc(50% - 5px)
  }

  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 120%;
    color: #A7A7A7;
  }

  &:hover{
    border-color: black;
    cursor: pointer;
  }

  &.user-valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat;
  }

  &.user-invalid {
    border-color: crimson;
  }
}

.question_prompt {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
}

.question_pagination {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  margin-top: 40px;
}

.question_next {
  border: 1px solid transparent;
  background: #2B4FA9;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  padding: 18px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
  min-width: 150px;
  box-sizing: border-box;

  &:disabled {
    border-color: #B8B8B8;
    background: white;
    color: #B8B8B8;

    & svg {
      fill: #B8B8B8;
    }

    &:hover {
      cursor: auto;
      background: white;
    }
  }

  &:hover {
    cursor: pointer;
    background: #2c4295;
  }

  & svg {
    margin-left: 8px;
  }
}

.question_prev {
  border: 1px solid #B8B8B8;
  background: white;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #B8B8B8;
  padding: 18px 0;
  margin-right: 25px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 150px;
  box-sizing: border-box;

  &:hover {
    cursor: pointer;
  }

  & svg {
    margin-right: 8px;
  }
}

.question_radio-wrapper {
  display: flex;
  flex-direction: row;
}

.question_radio {
  display: none;
}

.question_radio-label {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  color: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 50px;

  &::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #2B4FA9;
    border-radius: 50%;
    margin-right: 12px;
    background: none;
  }

  &:hover {
    cursor: pointer;
  }
}

.question_radio:checked + .question_radio-label::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='6' cy='6' r='6' fill='%232B4FA9'/%3E%3C/svg%3E%0A") center no-repeat;
}

select.question_text-input {
  appearance: none;
  background: url("data:image/svg+xml,%0A%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.99999 5.17205L11.95 0.222046L13.364 1.63605L6.99999 8.00005L0.635986 1.63605L2.04999 0.222046L6.99999 5.17205Z' fill='black'/%3E%3C/svg%3E%0A") 96% center no-repeat, #F9F9F9;
}

.question_subtitle {
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 140%;
  color: #000000;
  margin-top: 30px;
  margin-bottom: 80px;

  & span {
    color: #2C4295;
  }
}

.question_text {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 20px;
}

.question_button {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 110%;
  color: #FFFFFF;
  background: #2B4FA9;
  padding: 20px 40px;
  text-decoration: none;
  align-self: flex-start;
  border: none;
  text-align: center;

  &:hover {
    cursor: pointer;
  }
}

input[type="date"] {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120%;
  color: black;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  display: none;
}

.question_input-wrapper {
  max-width: 350px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.question_probability {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 12px;

  & span {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 140%;
    color: #3ED544;
  }
}

.question_protected {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
  color: #BD4D4E;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 20px;
  align-self: flex-start;

  &::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url("data:image/png;base64,") center no-repeat;
    margin-right: 8px;
  }
}

#accepted {
  display: none;
}

.accepted {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120%;
  color: #2B4FA9;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 20px;

  &:hover {
    cursor: pointer;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 1px solid #2B4FA9;
    margin-right: 8px;
    background: none;
  }
}

#accepted:checked + .accepted::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5.08333L4.46405 8L11 1' stroke='%232B4FA9' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
}

.question_submit {
  background: #2B4FA9;
  box-shadow: 0px 0px 40px rgba(43, 79, 169, 0.4);
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF;
  border: none;
  align-self: flex-start;
  padding: 17px 0;
  border: 1px solid transparent;
  min-width: 280px;

  &:hover {
    cursor: pointer;
  }

  &:disabled {
    border-color: #B8B8B8;
    background: white;
    color: #B8B8B8;
    box-shadow: none;

    &:hover {
      cursor: auto;
    }
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
}

@media(max-width: 767px) {
  .quiz {
    padding: 40px 0 150px;
  }
  .question {
    min-height: 290px;
  }

  .question_title {
    font-size: 24px;
    line-height: 110%;
    padding: 20px 0;
  }

  .question_body {
    padding: 30px 15px 20px;
  }

  .question_text-input {
    max-width: none;
  }

  .question_next,
  .question_prev {
    flex-grow: 1;
    min-width: auto;
  }

  .question_radio-wrapper {
    flex-direction: column;
  }

  .question_radio-label {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .question_button {
    width: 100%;
    box-sizing: border-box;
  }

  .question_label {
    margin-bottom: 8px;
    margin-top: 25px;
  }

  .question_subtitle {
    margin-bottom: 30px;
  }

  .question_submit {
    min-width: auto;
    width: 100%;
  }

  .question_input-wrapper {
    max-width: none;
  }
}
