.banner {
  background: linear-gradient(0deg, #2B4FA9, #2B4FA9);
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.banner_title {
  font-style: normal;
  font-weight: 500;
  font-size: 34px;
  line-height: 110%;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 40px;
}

.banner-question {
  display: none;
  flex-direction: column;
  width: 630px;
  background: white;
  padding: 40px 115px;
  box-sizing: border-box;

  &.active {
    display: flex;
  }
}

.banner_subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 120%;
  text-align: center;
  color: #000000;
  margin-bottom: 8px;
}

.banner_text-input {
  width: 100%;
  background: #F9F9F9;
  border: 1px solid #DFDFDF;
  box-sizing: border-box;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 110%;
  color: black;
  margin-top: 12px;
  padding: 14px 18px;

  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 110%;
    color: #B9B9B9;
  }

  &:hover {
    border-color: black;
  }

  &.user-valid {
    background: url("data:image/svg+xml,%0A%3Csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.646 14.818a.5.5 0 00.707 0l8.132-8.132A1 1 0 1119.9 8.1l-9.193 9.193a1 1 0 01-1.414 0l-4.95-4.95a1 1 0 111.414-1.414l3.89 3.89z' fill='%2315AF40'/%3E%3C/svg%3E") 98% center no-repeat;
  }

  &.user-invalid {
    border-color: crimson;
  }

  &--half {
    flex-basis: calc(50% - 5px);
  }
}

.banner_prompt {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
  display: inline-block;
  width: 100%;
  margin-top: 6px;
}

.banner_label {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #000000;
  display: inline-block;
  width: 100%;
  margin-top: 28px;
}

select.banner_text-input {
  appearance: none;
  background: url("data:image/svg+xml,%0A%3Csvg width='14' height='8' viewBox='0 0 14 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.99999 5.17205L11.95 0.222046L13.364 1.63605L6.99999 8.00005L0.635986 1.63605L2.04999 0.222046L6.99999 5.17205Z' fill='black'/%3E%3C/svg%3E%0A") 96% center no-repeat, #F9F9F9;
}

.banner-question--first .banner_pagination {
  justify-content: flex-end;
}

.banner_pagination {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
  width: 100%;
}

.banner_radio-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}

.residenceAddress {
  display: none;

  &.open {
    display: block;
  }
}

.banner_input-wrapper {
  max-width: 400px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

#accepted2 {
  display: none;
}

#accepted2:checked + .accepted::before {
  background: url("data:image/svg+xml,%0A%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5.08333L4.46405 8L11 1' stroke='%232B4FA9' stroke-width='2'/%3E%3C/svg%3E%0A") center no-repeat;
}

.banner_steps {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 40px;

  & span {
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 120%;
    color:#15AF40;
    display: flex;
    flex-direction: row;
    align-items: center;

    &.active {
      color: #15AF40;

      &::after {
        background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%2315AF40'/%3E%3C/svg%3E%0A");
      }
    }

    &.active ~ span {
      color:  #A7A8B0;

      &::after {
        background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%23A7A8B0'/%3E%3C/svg%3E%0A");
      }
    }

    &::before {
      content: "Шаг";
      margin-right: 3px;
    }

    &::after {
      content: "";
      display: inline-block;
      width: 11px;
      height: 18px;
      background-image: url("data:image/svg+xml,%0A%3Csvg width='11' height='18' viewBox='0 0 11 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.00051 9L0 1.99969L1.99974 0L11 9L1.99974 18L0 16.0003L7.00051 9Z' fill='%2315AF40'/%3E%3C/svg%3E%0A");
      margin-left: 11px;
      margin-right: 19px;
    }
  }
}

.banner_steps span:last-child::after {
  margin-right: 0;
}

@media(max-width: 767px) {
  .banner {
    padding: 80px 0;
  }

  .banner_title {
    font-size: 26px;
  }

  .banner form {
    width: 100%;
  }

  .banner-question {
    width: 100%;
    padding: 30px 15px;
  }

  .banner_input-wrapper {
    max-width: none;
  }

  .banner_radio-wrapper {
    flex-direction: column;
  }
}
