.terms {
  padding-bottom: 120px;
  padding-top: 40px;
}

.terms_title {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 50px;
  margin-top: 80px;
}

.terms_table {
  display: flex;
  flex-direction: column;
  overflow-x: scroll;

  &::-webkit-scrollbar {
    width: 0;
  }
}

.terms_row {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;

  &--title .terms_cell {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 110%;
    color: #FFFFFF;
    padding: 15px 0;
  }
}

.terms_cell {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 140%;
  color: #000000;
  padding: 18px 0;
  flex-shrink: 0;

  &:not(:last-of-type) {
    border-right: 1px solid #CFCFCF;
  }
}

.terms_table--one {

  & .terms_cell {
    background: #F0F2F5;
  }

  & .terms_row--title .terms_cell {
    background: #2C4295;
  }

  & .terms_cell {
    &:first-of-type {
      width: 223px
    }

    &:nth-of-type(2) {
      width: 315px
    }

    &:nth-of-type(3) {
      width: 631px
    }
  }
}

.terms_table--two {

  & .terms_row:nth-of-type(odd):not(.terms_row--title) .terms_cell {
    background: #F0F2F5;
  }

  & .terms_row--title .terms_cell {
    background: #2C4295;
  }

  & .terms_cell {
    &:first-of-type {
      width: 180px
    }

    &:nth-of-type(2) {
      width: 181px
    }

    &:nth-of-type(3) {
      width: 171px
    }

    &:nth-of-type(4) {
      width: 264px
    }

    &:nth-of-type(5) {
      width: 370px
    }
  }
}

.terms_description {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #000000;
  margin-top: 24px;
}

@media(max-width: 767px) {
  .terms {
    padding: 70px 15px 30px;
  }

  .terms_title {
    font-size: 26px;
    margin-top: 50px;
    margin-bottom: 30px;
  }
}
