.advantages {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 120px;
}

.advantages_item {
  background: #F0F2F5;
  flex-basis: 31.6%;
  flex-shrink: 0;
  padding: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages_title {
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 140%;
  color: #000000;
  margin-bottom: 15px;

  & span {
    font-style: normal;
    font-weight: 500;
    font-size: 50px;
    line-height: 100%;
    text-transform: uppercase;
    color: #000000;
  }
}

.advantages_description {
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 140%;
  text-align: center;
  color: #000000;
}

@media(max-width: 767px) {
  .advantages {
    flex-direction: column;
  }

  .advantages_item {
    margin-bottom: 20px;
  }
}
