.credits {
  background: #F0F2F5;
  padding: 120px 0;
  margin-bottom: 90px;

  & .center {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.credits_title {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-align: center;
  color: #100F0F;
  margin-bottom: 50px;
  display: inline-block;
  width: 100%;
}

.credits_item {
  padding: 30px;
  flex-basis: 31.6%;
  flex-shrink: 0;
  background: linear-gradient(0deg, #0082C9, #0082C9);
  box-sizing: border-box;
  margin-bottom: 30px;
  min-width: 200px;
}

.credits_subtitle {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
  color: #FFFFFF;
  margin-top: 40px;
  margin-bottom: 20px;
  word-break: break-all;
}

.credits_button {
  display: inline-block;
  text-decoration: none;
  background: white;
  padding: 13px 22px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 110%;
  text-align: center;
  color: #203586;
  transition: all 0.3s;

  &:hover {
    background: #2c4295;
    cursor: pointer;
    color: white;
  }
}

@media(max-width: 767px) {
  .credits {
    padding: 120px 0 100px;
    margin-bottom: 0;

    & .center {
      flex-direction: column;
    }
  }

  .credits_title {
    font-size: 26px;
    margin-bottom: 50px;
  }

  .credits_item svg {
    width: 50px;
    height: 50px;
  }

  .credits_subtitle {
    margin-top: 20px;
  }
}
